.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
  background: rgb(255, 212, 172);
  height: 100%;
  padding: 10px;
}
.personDialogs {
    background: rgb(224, 130, 130);
}
.personDialog {
    background: rgb(253, 223, 223);
    padding: 10px;
    margin-top: 5px;
}
.personDialogs .activeDialog {
    color: royalblue;
}
.messages {
    background: rgb(173, 243, 141);
}
.message {
    background: rgb(198, 255, 206);
    padding: 10px;
    margin-top: 5px;
}